body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

form{
  padding: 30px;
  margin-top: 60px;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
}

form button, form h5{
  margin: 20px 0;
}

form button {
  padding: 15px;
}

input[type=text]:not(.browser-default):focus:not([readonly]),
input[type=email]:not(.browser-default):focus:not([readonly]),
input[type=password]:not(.browser-default):focus:not([readonly]),
textarea.materialize-textarea:focus:not([readonly]){
  border-color: rgb(0, 140, 255);
  box-shadow: none;
}
.hero {
  height: 120px;
  background-size: cover;
  background-position: center;
  position: relative;
}

.hero::after {
  content: "";
  display: block;
  opacity: .4;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: black;
}

.hero * {
  position: relative;
  z-index: 2;
}

.hero h1, .hero h2 {
  color: white;
}

.hero h1 {
  font-size: 60px;
  /* padding-top: 10px; */
  margin-top: 0;
}

.panel-default > .panel-heading {
    background-color:rgb(167, 12, 12);
}

.card > .card-header {
    background-color:rgba(184, 218, 255, 0.705);
}


nav.navbar.navbar-default {
  margin-bottom: 0;
}

#nav{
  /* background-color: #2377be !important; */
  background-color: #353233 !important;
}
.footer {
  min-height: 100px;
  background: #2377be;
  position: relative;
}

.footer span {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 3px;
  background: #185586;
  color: white
}
.wrapper {
  min-height: 100%;
  padding-bottom: 100px;
}
.App {
  text-align: center;
}



